import template from './sw-cms-el-zen-footer-navigation.html.twig';
import './sw-cms-el-zen-footer-navigation.scss';

const {Component} = Shopware;

Component.register('sw-cms-el-zen-footer-navigation', {
    template,

    mixins: [
        'cms-element'
    ],

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('zen-footer-navigation');
        }
    }
});
