import template from './sw-cms-block-zen-grid-9-3.html.twig';
import './sw-cms-block-zen-grid-9-3.scss';

const { Component } = Shopware;

Component.register('sw-cms-block-zen-grid-9-3', {
    template,

    created() {
        $parent.block.isZenGravityBlock = true;

        if (!$parent.block.customFields) {
            this.$set($parent.block, 'customFields', {});
        }
    },

    computed: {
        style() {
            if ($parent.block.customFields && $parent.block.customFields.zenit_grid_gap) {
                return {gridGap: $parent.block.customFields.zenit_grid_gap}
            }
        }
    }
});